.second-wrap p { padding-top: 15px; padding-bottom: 15px; border-top: 1px solid #ff22bb; border-bottom: 1px solid #5cb4ff; }
p.fcaP { border-top: none; border-bottom: none; margin-bottom: 40px; }
.second-img-wrap {margin-top: 150px; padding-top: 50px; padding-bottom: 65px; margin-bottom: 130px; border-top: 1px solid #ff22bb; border-bottom: 1px solid #5cb4ff; max-width: 91%; margin-left: auto; margin-right: auto;}
.second-img-wrap img {position: relative; -webkit-box-shadow: 2px 2px 15px rgba(0,0,0,0.1); box-shadow: 2px 2px 15px rgba(0,0,0,0.1); max-width: 90%; border-radius: 5px; -webkit-transition: -webkit-transform 1s; transition: -webkit-transform 1s; -o-transition: transform 1s; transition: transform 1s; transition: transform 1s, -webkit-transform 1s; border-radius:5px;-webkit-transform-style: preserve-3d;transform-style: preserve-3d; }
.second-img-wrap img:hover {-webkit-box-shadow: 2px 2px 15px rgba(255, 34, 187, 0.45);box-shadow: 2px 2px 15px rgba(255, 34, 187, 0.45);}



